.messages {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #F4F4F5;
}

.messages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.messages__item {
  margin-top: 15px;
}

.messages__item--sent {
  align-self: flex-end;
  display: flex;
  margin-right: 30px;
}

.messages__item--sent p {
  width: fit-content;
  margin: 0 20px 0 0;
  padding: 5px 8px;
  line-height: 35px;
  color: #ffffff;
  background-color: #0E61FA;
  border-radius: 10px;
  position: relative;

  &::after {
    position:absolute;
    content:"";
    width:30px;
    height:30px;
    right:-14px;
    top:10px;
    transform: scaleY(0.5) rotate(45deg);
    background-color: #0E61FA;
    box-shadow: 5px -5px 10px #C0C0C0;
    z-index: -1500;
  }
}

.messages__item svg {
  width: 43px;
  height: fit-content;
}

.messages__text {
  min-height: 70px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
}

.messages__textarea {
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  border: none;
  resize: none;

  &::placeholder {
    font-family: "TT Norms", "Arial", sans-serif;
  }
}


.messages__send-btn {
  width: 70px;
  padding: 0 20px;
  border: none;
  background-color: transparent;
  border-left: 1px solid #eeedec;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}

.messages__send-btn svg {
  width: 100%;
  height: auto;
}
